import Answer from "../components/Answer";
import getInput from "../components/Input";
import DayLayout from "../components/DayLayout";
import { notFound } from "next/navigation";

// Import day-specific solution functions
import { solveDay1 } from "./solutions/day1";
import { solveDay2 } from "./solutions/day2";

type DaySolver = (input: string) => Promise<{ partA: number; partB: number }>;

const solvers: Record<string, DaySolver> = {
    "day1": solveDay1,
    "day2": solveDay2,
};

export default async function DayPage({ params }: { params: Promise<{ day: string }> }) {
    const { day } = await params;
    
    // Check if the day exists
    if (!solvers[day]) {
        notFound();
    }

    const input = await getInput({ path: `${day}/input.txt` });
    const { partA, partB } = await solvers[day](input);

    return (
        <DayLayout day={`${day.charAt(0).toUpperCase() + day.slice(1)} Page`}>
            Part1: <Answer a={partA} />
            Part2: <Answer a={partB} />
        </DayLayout>
    );
}

// Generate static params for all available days
export async function generateStaticParams() {
    return Object.keys(solvers).map((day) => ({
        day,
    }));
}
